import { ModuleWithProviders }  from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { Admin } from '../components/admin/index';
import { AuthGuard } from '../auth.guard';

import { UsersManagement } from '../components/admin/users/index';
import { BlogsManagement } from '../components/admin/blogs/index';
import { BlogManagement } from '../components/admin/blogs/blog/index';

const routes: Routes = [{
    path: 'admin',
    component: Admin,
    canActivate: [ AuthGuard ],
    children: [
        {
            path: '',
            redirectTo: 'users',
            pathMatch: 'full'
        },
        {
            path: 'users',
            component: UsersManagement
        },
        {
            path: 'blog',
            component: BlogsManagement
        },
        {
            path: 'blog/:action',
            component: BlogManagement
        },
        {
            path: 'blog/:action/:id',
            component: BlogManagement
        }
    ]
}];

export const AdminRoutes: ModuleWithProviders = RouterModule.forRoot(routes);